body{margin:0;padding:0;font-family:Arial, Helvetica, sans-serif;transition:.2s all ease-in}body.dark{background:#343a40;color:#fff}button{cursor:pointer}input,button{outline:none;background:transparent;border:none}body.ant-scrolling-effect{width:100% !important}div.ant-image-preview{-webkit-transform-origin:50% 12% !important;transform-origin:50% 12% !important}img.ant-image-preview-img{width:100%;object-fit:scale-down}.ant-slider{width:98%;padding-top:6px;-webkit-transform:translateY(50%);transform:translateY(50%)}.ant-slider:hover .ant-slider-track{background-color:#E2AC5A}.ant-slider:hover .ant-slider-handle{border:solid 4px #DA952D !important}.ant-slider-rail{display:none}.ant-slider-step{padding-left:60px;border-radius:3px;height:6px;background:#888}.ant-slider-track{background-color:#DA952D;height:6px;z-index:1}.ant-slider-dot{display:none}.ant-slider-handle{margin-top:-12px;z-index:2;-webkit-transform:translate3d(-50%, 3px, 0px) !important;transform:translate3d(-50%, 3px, 0px) !important;width:24px;height:24px;border:solid 4px #DA952D !important}.ant-slider-handle-dragging{border:solid 4px #B67A20 !important;box-shadow:0 0 0 5px rgba(141,141,141,0.12) !important}.ant-slider-handle:focus{border:solid 4px #DA952D !important;box-shadow:0 0 0 5px rgba(141,141,141,0.12) !important}.ant-slider-mark{top:-24px;left:calc(1.5%);width:calc(97%)}.ant-slider-mark-text{width:60px}.ant-switch-checked{background-color:#DA952D}

.main-page{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100vh}.main-page__title{margin:0;padding-bottom:100px;text-transform:uppercase;font-size:50px;font-family:'Roboto', sans-serif;font-weight:500}.main-page__icon{box-shadow:4px 4px 8px 1px rgba(34,60,80,0.29);border:1px solid #ccc;border-radius:50%;padding:15px;display:block}.main-page__icon :hover,.main-page__icon :active{fill:#DA952D}.search-icon_home{font-size:50px}

.loginForm{max-width:300px;margin:50px auto;padding:20px;border-radius:5px;box-shadow:4px 4px 8px 1px rgba(34,60,80,0.29)}.loginForm__title{text-align:center}.loginForm__button{width:100%}.loginForm__forgot{float:right}.ant-col-rtl .loginForm__forgot{float:left}

.regForm{max-width:300px;margin:50px auto;padding:20px;border-radius:5px;box-shadow:4px 4px 8px 1px rgba(34,60,80,0.29)}.regForm__title{text-align:center}.regForm__button{width:100%}

.button{width:48%;font:600 12px 'Roboto', sans-serif;color:#424242;border:1px solid #424242;border-radius:10px;background-color:#f5f5f5;text-transform:uppercase;padding:10px 0;transition:300ms ease-out background-color, 300ms ease-out color, 300ms ease-out border}.button:first-child{margin-right:10px}.button:last-child{margin-top:10px}.button:hover{background-color:#b8b5b5;color:#fff;border:1px solid #b8b5b5}.button:active{background-color:#f5f5f5}

.filmCard--wrapper{min-width:151px;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#f5f5f5}.filmCard__poster{width:100%;height:35vh;background-color:black;display:-webkit-flex;display:flex;object-fit:cover}.filmCard__poster .ant-image{width:100%}.filmCard__poster__background{height:35vh;position:fixed;width:100%;-webkit-filter:blur(7px);filter:blur(7px)}.filmCard__poster__image{width:100%;height:100%;object-fit:scale-down}.filmCard__infoBlock{width:100%;position:fixed;top:35vh;max-height:49.5vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex:1 0 auto;flex:1 0 auto;border-radius:35px 35px 0 0;background-color:#f5f5f5;font:400 16px "Roboto",sans-serif;color:#707070;margin-top:-50px;padding:19px 15px 0;margin-bottom:152px}.filmCard__infoBlock--titleWrapper{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:0 10px}.filmCard__infoBlock__title{width:80%;font:500 25px "Roboto",sans-serif;color:#000;text-transform:uppercase;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto;margin:0}.filmCard__infoBlock__rate{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-self:flex-start;align-self:flex-start;font:400 20px "Roboto",sans-serif;color:#dfa01a;margin:0 0 0 5px}.filmCard__infoBlock--secondTitleWrapper{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin:2px 0 12px;padding:0 10px}.filmCard__infoBlock__secondTitle{width:50%;text-transform:capitalize;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto;margin:0}.filmCard__infoBlock__year{width:50%;text-align:right;margin:0}.filmCard__infoBlock__genre{text-align:center;margin:0 0 7px}.filmCard__infoBlock--underline{width:75%;color:#707070;margin:0 auto 13px}.filmCard__infoBlock--scrollableWrapper{overflow:auto}.filmCard__infoBlock__description{text-align:justify;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto;margin:0}.filmCard__infoBlock__description--label{width:100%;max-height:150px;overflow:hidden;position:relative;transition:ease-in 800ms all}.filmCard__footer{width:100%;position:fixed;bottom:0px;height:152px;background-color:#f5f5f5;-webkit-flex:0 0 auto;flex:0 0 auto}.filmCard__footer__buttonGroup{width:100%;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;margin:10px 0;padding:0 15px}.filmCard__footer__emptyBlock{width:100%;height:46px}.inputDesc{position:absolute;opacity:0}.inputDesc:checked ~ .filmCard__infoBlock__description--label{max-height:350px}

.navigation-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-align-content:center;align-content:center;position:fixed;width:100%;height:50px;bottom:0;background-color:#eee;background:linear-gradient(0deg, #ccc, #eee);border-top:1px solid #ccc}.navigation-buttons{fill:#888}.navigation-buttons__checked{fill:#DA952D}.ant-radio{display:none}.divition-line{height:38px;width:1px;background-color:#888;opacity:0.6;-webkit-align-self:center;align-self:center}.search-icon_small{font-size:30px}

.settings--wrapper{overflow:hidden;min-width:151px;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background-color:#f5f5f5}.settings__header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background-color:#fff;height:50px;box-shadow:0 3px 4px rgba(0,0,0,0.2)}.settings__header--title{margin:0 !important;text-transform:uppercase}.settings__content{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;padding-bottom:50px}.settings__content--row{padding-left:6px;width:90%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.settings__content--row span{color:#707070}.settings__content--slider{width:78%}.settings__content--select{width:80%}.settings__content--login{width:90%;border-radius:10px;background-color:#DA952D;color:white;border:2px solid #DA952D;text-transform:uppercase;font-weight:bold}.settings__content--reset{width:90%;border-radius:10px;background-color:transparent;color:#DA952D;border:2px solid #DA952D;text-transform:uppercase;font-weight:bold}

