@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import '../../styles/variables.scss';


.settings {
  &--wrapper {
    overflow: hidden;
    min-width: 151px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: $templateBackgroundColor;
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    height: $headerHeight;
    box-shadow: 0 3px 4px rgba(0,0,0, 0.2);
  }
  &__header--title {
    margin: 0 !important;
    text-transform: uppercase;
  }
  &__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding-bottom: $navigationHeight;
  }
  &__content--row {
    span {
      color: $textColor;
    }
    padding-left: 6px;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__content--slider {
    width: 78%;
  }
  &__content--select {
    width: 80%;
  }
  &__content--login {
    width: 90%;
    border-radius: 10px;
    background-color: $activbtn;
    color: white;
    border: 2px solid $activbtn;
    text-transform: uppercase;
    font-weight: bold;
  }
  &__content--reset {
    width: 90%;
    border-radius: 10px;
    background-color: transparent;
    color: $activbtn;
    border: 2px solid $activbtn;
    text-transform: uppercase;
    font-weight: bold;
  }
}